<template>
  <el-container>
    <el-form ref="AccountFrom" :model="account" :rules="rules" label-position="left" label-width="0px"
             class="demo-ruleForm login-container">
      <h3 class="title">农情信息服务云平台</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="account.username" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="pwd">
        <el-input type="password" v-model="account.pwd" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <!--<el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>-->
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleLogin" :loading="loading">登录</el-button>
      </el-form-item>
    </el-form>
    <el-footer>Copyright © 2012 北京世纪国源科技股份有限公司</el-footer>
  </el-container>
</template>
<script>
import API from "../api/api_user";
import funcMenu from  '../common/funcMenu'
import mountFuncController from '../common/mountFuncMenu'
import Cookies from 'js-cookie';
export default {
  data() {
    return {
      loading: false,
      account: {
        username: "",
        pwd: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入账号", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入密码", trigger: "blur" }
        ]
      },
      checked: true
    };
  },
  methods: {
    handleLogin() {
      let that = this;
      this.$refs.AccountFrom.validate(valid => {
        if (valid) {
          this.loading = true;
          let loginParams = {
            username: this.account.username,
            pwd: this.account.pwd,
            loginType:'web'
          };
          API.login(loginParams)
            .then(
              function(result) {
                that.loading = false;
                if (result && result.code === 0) {
                  debugger
                  that.$store.commit('LOGIN',result);

                  //先重置各种注册中心，20180911高飞添加
                  funcMenu.resetFuncMenu();//登录之前清空模块注册中心
                  mountFuncController.resetMountFuncMenu();//登录之前清空MountFuncMenu注册中心
                  //跳转至首页（App.vue）
                  that.$router.push({ path: "/" });
                } else {
                  that.$message.error({
                    showClose: true,
                    message: result.msg || "登录失败",
                    duration: 2000
                  });
                }
              },
              function(err) {
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.message,
                  duration: 2000
                });
              }
            ).catch(function(error) {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: error.message,
                duration: 2000
              });
            });
        }
      });
    }
  }
};
</script>

<style >
body {
  background: #dfe9fb;
  background-repeat:no-repeat;
  background-size:100% 100%;
  -moz-background-size:100% 100%;
  background-image: url(../assets/images/loginbackground.png);
}
</style >
<style scoped>
.login-container {
  /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 160px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;

  background: -ms-linear-gradient(top, #ace, #00c1de); /* IE 10 */
  background: -moz-linear-gradient(top, #ace, #00c1de); /*火狐*/
  background: -webkit-gradient(
    linear,
    0% 0%,
    0% 100%,
    from(#ace),
    to(#00c1de)
  ); /*谷歌*/
  background: -webkit-linear-gradient(
    top,
    #ace,
    #00c1de
  ); /*Safari5.1 Chrome 10+*/
  background: -o-linear-gradient(top, #ace, #00c1de); /*Opera 11.10+*/

  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}
.el-container{
  height:100%;
}
.el-footer{
  height: 25px !important;
  position: absolute;
  bottom: 0;
  line-height: 25px;
  color: #e1e7e7;
  width: 100%;
  text-align: center;
  font:"Microsoft Yahei";
  font-size: 12px;
  letter-spacing: 1px;
}
</style>



