exports.push([module.id, "\nbody {\n  background: #dfe9fb;\n  background-repeat:no-repeat;\n  background-size:100% 100%;\n  -moz-background-size:100% 100%;\n  background-image: url(" + escape(require("../assets/images/loginbackground.png")) + ");\n}\n", "", {"version":3,"sources":["D:/meiliuchun/agrisituation农情平台/agrisituation-ui/src/components/src/components/Login.vue"],"names":[],"mappings":";AAmGA;EACA,oBAAA;EACA,4BAAA;EACA,0BAAA;EACA,+BAAA;EACA,gDAAA;CACA","file":"Login.vue","sourcesContent":["<template>\n  <el-container>\n    <el-form ref=\"AccountFrom\" :model=\"account\" :rules=\"rules\" label-position=\"left\" label-width=\"0px\"\n             class=\"demo-ruleForm login-container\">\n      <h3 class=\"title\">农情信息服务云平台</h3>\n      <el-form-item prop=\"username\">\n        <el-input type=\"text\" v-model=\"account.username\" auto-complete=\"off\" placeholder=\"账号\"></el-input>\n      </el-form-item>\n      <el-form-item prop=\"pwd\">\n        <el-input type=\"password\" v-model=\"account.pwd\" auto-complete=\"off\" placeholder=\"密码\"></el-input>\n      </el-form-item>\n      <!--<el-checkbox v-model=\"checked\" checked class=\"remember\">记住密码</el-checkbox>-->\n      <el-form-item style=\"width:100%;\">\n        <el-button type=\"primary\" style=\"width:100%;\" @click.native.prevent=\"handleLogin\" :loading=\"loading\">登录</el-button>\n      </el-form-item>\n    </el-form>\n    <el-footer>Copyright © 2012 北京世纪国源科技股份有限公司</el-footer>\n  </el-container>\n</template>\n<script>\nimport API from \"../api/api_user\";\nimport funcMenu from  '../common/funcMenu'\nimport mountFuncController from '../common/mountFuncMenu'\nimport Cookies from 'js-cookie';\nexport default {\n  data() {\n    return {\n      loading: false,\n      account: {\n        username: \"\",\n        pwd: \"\"\n      },\n      rules: {\n        username: [\n          { required: true, message: \"请输入账号\", trigger: \"blur\" }\n        ],\n        pwd: [\n          { required: true, message: \"请输入密码\", trigger: \"blur\" }\n        ]\n      },\n      checked: true\n    };\n  },\n  methods: {\n    handleLogin() {\n      let that = this;\n      this.$refs.AccountFrom.validate(valid => {\n        if (valid) {\n          this.loading = true;\n          let loginParams = {\n            username: this.account.username,\n            pwd: this.account.pwd,\n            loginType:'web'\n          };\n          API.login(loginParams)\n            .then(\n              function(result) {\n                that.loading = false;\n                if (result && result.code === 0) {\n                  debugger\n                  that.$store.commit('LOGIN',result);\n\n                  //先重置各种注册中心，20180911高飞添加\n                  funcMenu.resetFuncMenu();//登录之前清空模块注册中心\n                  mountFuncController.resetMountFuncMenu();//登录之前清空MountFuncMenu注册中心\n                  //跳转至首页（App.vue）\n                  that.$router.push({ path: \"/\" });\n                } else {\n                  that.$message.error({\n                    showClose: true,\n                    message: result.msg || \"登录失败\",\n                    duration: 2000\n                  });\n                }\n              },\n              function(err) {\n                that.loading = false;\n                that.$message.error({\n                  showClose: true,\n                  message: err.message,\n                  duration: 2000\n                });\n              }\n            ).catch(function(error) {\n              that.loading = false;\n              that.$message.error({\n                showClose: true,\n                message: error.message,\n                duration: 2000\n              });\n            });\n        }\n      });\n    }\n  }\n};\n</script>\n\n<style >\nbody {\n  background: #dfe9fb;\n  background-repeat:no-repeat;\n  background-size:100% 100%;\n  -moz-background-size:100% 100%;\n  background-image: url(../assets/images/loginbackground.png);\n}\n</style >\n<style scoped>\n.login-container {\n  /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/\n  -webkit-border-radius: 5px;\n  border-radius: 5px;\n  -moz-border-radius: 5px;\n  background-clip: padding-box;\n  margin: 160px auto;\n  width: 350px;\n  padding: 35px 35px 15px 35px;\n  background: #fff;\n  border: 1px solid #eaeaea;\n  box-shadow: 0 0 25px #cac6c6;\n\n  background: -ms-linear-gradient(top, #ace, #00c1de); /* IE 10 */\n  background: -moz-linear-gradient(top, #ace, #00c1de); /*火狐*/\n  background: -webkit-gradient(\n    linear,\n    0% 0%,\n    0% 100%,\n    from(#ace),\n    to(#00c1de)\n  ); /*谷歌*/\n  background: -webkit-linear-gradient(\n    top,\n    #ace,\n    #00c1de\n  ); /*Safari5.1 Chrome 10+*/\n  background: -o-linear-gradient(top, #ace, #00c1de); /*Opera 11.10+*/\n\n  .title {\n    margin: 0px auto 40px auto;\n    text-align: center;\n    color: #505458;\n  }\n  .remember {\n    margin: 0px 0px 35px 0px;\n  }\n}\n.el-container{\n  height:100%;\n}\n.el-footer{\n  height: 25px !important;\n  position: absolute;\n  bottom: 0;\n  line-height: 25px;\n  color: #e1e7e7;\n  width: 100%;\n  text-align: center;\n  font:\"Microsoft Yahei\";\n  font-size: 12px;\n  letter-spacing: 1px;\n}\n</style>\n"],"sourceRoot":""}]);

